<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家返利管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看厂家返利单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="rebateInfo" class="demo-form-inline">
        <el-form-item label="采购组织：" size="mini">
          {{rebateInfo.company_name}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{rebateInfo.department_name}}
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          {{rebateInfo.buyer_name}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{rebateInfo.business_time}}
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          {{rebateInfo.rebate_sn}}
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          {{rebateInfo.supplier_name}}
        </el-form-item>
        <el-form-item label="品牌：" size="mini">
          {{rebateInfo.brand_name}}
        </el-form-item>
        <el-form-item label="返利金额" size="mini">
          （{{rebateInfo.share_mode_name}}）：{{rebateInfo.total_price| priceFormatNone}} 元
        </el-form-item>
        <el-form-item label="返利类型：" size="mini">
          {{rebateInfo.rebate_type_name}}
        </el-form-item>
        <el-form-item label="返利周期：" size="mini">
          {{rebateInfo.cycle_name}}
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          {{rebateInfo.auditor_status_name}}
        </el-form-item>
        <el-form-item label="备注：" size="mini">
          {{rebateInfo.remark}}
        </el-form-item>

      </el-form>
    </div>
    <div class="table_container">
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="tableData" style="width: 100%" show-summary :summary-method="getSummaries" max-height="600">
          <el-table-column :show-overflow-tooltip="true" property="instorage_sn" label="单据号" min-width="180"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="160"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购价格" min-width="160" align="right">
            <template slot-scope="scope">
              <pre>{{scope.row.purchase_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_num" label="入库数量" min-width="120" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="入库金额" align="right" min-width="120">
            <template slot-scope="scope">
              <pre>{{scope.row.subtotal_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" min-width="160">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="type_name" label="入库类型" min-width="120">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_price" label="分摊返利金额" min-width="120" align="right">
          </el-table-column>
        </el-table>
      </div>
      <p class="other_p">返利总金额：{{rebateInfo.total_price}}</p>
      <div class="refundauditFoot">
        <span v-if="rebateInfo.update_user_name!=''">修改人：{{rebateInfo.update_user_name}}</span>
        <span v-if="rebateInfo.update_user_name==''">创建人：{{rebateInfo.add_user_name}}</span>
        <span v-if="rebateInfo.update_time!=''">修改时间：{{rebateInfo.update_time}}</span>
        <span v-if="rebateInfo.update_time==''">创建时间：{{rebateInfo.add_time}}</span>
      </div>
      <div slot="footer" class="dialog-footer">
        <!--未审核-->
        <template v-if="rebateInfo.auditor_status==1">
          <el-button type="primary" @click="examineForm" v-if="authority.b_pass==1">审 核</el-button>
          <el-button @click="abolishForm" v-if="authority.b_invalid==1">作 废</el-button>
          <el-button @click="goBack">返 回</el-button>
        </template>
        <!--作废-->
        <template v-if="rebateInfo.auditor_status==2 || rebateInfo.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
          <el-button plain @click="AUDIT_GET" v-if="rebateInfo.auditor_status!=3">弃审</el-button>
          <el-button @click="goBack">返 回</el-button>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchaseSearchRebatemanage",
  data() {
    return {
      authority: {},
      rebateInfo: {},
      tableData: [],
      checkPage: "",
      rebate_id: ""
    };
  },
  computed: {
    ...mapGetters(["purchaseSearch_showGet"])
  },

  methods: {
    ...mapActions([
      "purchaseDefault_show",
      "rebateauditDefault_show",
      "rebatefindDefault_show"
    ]),
    AUDIT_GET() {
      let rebate_id = this.purchaseSearch_showGet.rebate_id;
      this.$axMethod
        .auidtorNoPassRebatemanage({
          rebate_id
        })
        .then(response => {
          if (response.data.code == 200) {
            this.$message({
              message: response.data.data,
              type: "success"
            });
            this.goBack();
          }
        });
    },
    examineForm() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.rebateAuditorPass,
        data: {
          rebate_id: this.rebate_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishForm() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.rebateAuditorInvalid,
        data: {
          rebate_id: this.rebate_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    goBack() {
      if (this.checkPage == "rebateauditPage") {
        this.rebateauditDefault_show({
          //commont: "rebateauditDefault"
        });
        this.$router.push({ path: "/purchase/rebatemanage" });
      } else if (this.checkPage == "rebatefindPage") {
        this.rebatefindDefault_show({
          //commont: "rebatefindDefault"
        });
        this.$router.push({ path: "/purchase/rebatemanage" });
      } else {
        this.purchaseDefault_show({
          //commont: "rebatemanageDefault",
          params: this.purchaseSearch_showGet.params,
          goPage: this.purchaseSearch_showGet.goPage,
          pageSize: this.purchaseSearch_showGet.pageSize
        });
        this.$router.push({ path: "/purchase/rebatemanage" });
      }
    },
    getSummaries(param) {
      // 合并计算
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        if (column.property === "subtotal_price") {
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return prev + curr;
          }, 0);
          this.totalPrice = Number(sums[index] || 0);
          sums[index] = Number(sums[index] || 0)
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        }
        if (column.property === "rebate_price") {
          sums[index] = this.rebateInfo.total_price;
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    var vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getRebateInfo,
      params: {
        rebate_id: this.purchaseSearch_showGet.rebate_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.rebateInfo = data.data.rebateInfo;
        this.$set(this, "tableData", data.data.rebateGoodsInfo);
      }
    });
    this.checkPage = this.purchaseSearch_showGet.checkPage;
    this.rebate_id = this.purchaseSearch_showGet.rebate_id;
  }
};
</script>

